
.close-popin-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.network-header {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
}
.network-title {
  display: flex;
  flex: 1 1;
}
.partners.search-autocomplete .ant-input {
  background: white;
}
.modal-search-partners .ant-modal-content {
  background: transparent;
}
.modal-search-partners .close-popin-btn {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
  cursor: pointer;
}
.modal-search-partners .ant-modal-body {
  padding: 0;
}
.modal-search-partners .ant-modal-close {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
  color: white;
}
.modal-search-partners .partner-card.ant-card {
  cursor: pointer;
  border-radius: 5px;
}
.modal-search-partners .partner-card-address {
  font-weight: 500;
  font-size: 13px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: center;
  line-height: 18px;
}
.modal-search-partners .network-card-invoice {
  text-align: center;
}
.modal-search-partners .network-card-invoice-title {
  font-weight: 600;
  font-size: 10px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: center;
  line-height: 11px;
  text-transform: uppercase;
}
.modal-search-partners .network-card-invoice-value {
  font-weight: 500;
  font-size: 15px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 21px;
}
.modal-search-partners .network-card-footer {
  font-weight: 500;
  font-size: 11px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 21px;
}

